import React, { Component, Fragment } from "react";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";

class Column extends Component {
    state = {
        data:[
            {name:"", y:this.props.parent},
            {name:"", y:this.props.child}
        ]
    }
    render() {
        const series2 = this.state.data;    //App.js   
        const options = {
            chart: {
                type: 'pie',
                width: 200,
                height: 250			// bar.    line chart .
            },
            title: {
                text: '',
                style:{
                    fontSize:'15px',
                },
                y: 30,
            },
            credits: {
                enabled: false
            },

            tooltip: {
                enabled:true
            },
            
            plotOptions: {
                series: {
                    dataLabels: {
                        enabled: true,
                        formatter: function(){
                            var name = this.point.name
                            //var percentage = Math.round(this.percentage*100)/100 + ' %'
                            var result = name;
                            return result
                        },
                        distance: -40
                    },
                },

                pie:{

                }
            },
            series: [{ name: '' , data: series2 }]

        }
        return (
            <Fragment>
                <HighchartsReact highcharts={Highcharts} options={options} />
            </Fragment>
            
        );
    }
}
export default Column;